"use client";

import { useState } from "react";
import { DashboardWrapper } from "@/components/layout/dashboard-wrapper";
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { CalendarPlus, TrendingUp, Star, BookOpen, Search } from "lucide-react";
import { ScheduleSessionModal } from "@/components/scheduling/schedule-session-modal";
import { format } from 'date-fns';
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogTrigger,
} from "@/components/ui/dialog";

// Refined Interface with rating
interface Student {
  id: string;
  name: string;
  avatar: string;
  email: string;
  subjects: string[];
  progress?: number;
  averageRating?: number;
  lastSession?: Date; // Use Date object
}

// Updated mock data with ratings and Date objects
const mockStudents: Student[] = [
  {
    id: "1",
    name: "John Doe",
    avatar: "/avatars/john-doe.png",
    email: "john.doe@example.com",
    subjects: ["Calculus", "Physics"],
    progress: 85,
    averageRating: 4.8,
    lastSession: new Date(2024, 2, 15), // March 15, 2024
  },
  {
    id: "2",
    name: "Jane Smith",
    avatar: "/avatars/jane-smith.png",
    email: "jane.smith@example.com",
    subjects: ["Chemistry", "Biology"],
    progress: 78,
    averageRating: 4.9,
    lastSession: new Date(2024, 2, 14), // March 14, 2024
  },
];

export default function TutorStudentsPage() {
  const [isScheduleModalOpen, setIsScheduleModalOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);
  const [isInviteModalOpen, setIsInviteModalOpen] = useState(false);
  const [inviteEmail, setInviteEmail] = useState("");

  const handleScheduleClick = (student: Student) => {
    setSelectedStudent(student);
    setIsScheduleModalOpen(true);
  };

  const handleInvite = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("Inviting student with email:", inviteEmail);
    setInviteEmail("");
    setIsInviteModalOpen(false);
  };

  return (
    <DashboardWrapper>
      <div className="container mx-auto py-8">
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-2xl font-bold">My Students</h1>
            <p className="text-muted-foreground">Manage students and schedule sessions.</p>
          </div>
          <div className="flex gap-2">
            <div className="relative">
              <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input placeholder="Search students..." className="pl-8" />
            </div>
            <Dialog open={isInviteModalOpen} onOpenChange={setIsInviteModalOpen}>
              <DialogTrigger asChild>
                <Button>Invite Student</Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Invite a Student</DialogTitle>
                  <DialogDescription>
                    Enter the student's email address to send them an invitation.
                  </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleInvite} className="space-y-4">
                  <div className="space-y-2">
                    <label htmlFor="email" className="text-sm font-medium">
                      Email Address
                    </label>
                    <Input
                      id="email"
                      type="email"
                      placeholder="student@example.com"
                      value={inviteEmail}
                      onChange={(e) => setInviteEmail(e.target.value)}
                      required
                    />
                  </div>
                  <div className="flex justify-end gap-2">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => setIsInviteModalOpen(false)}
                    >
                      Cancel
                    </Button>
                    <Button type="submit">Send Invitation</Button>
                  </div>
                </form>
              </DialogContent>
            </Dialog>
          </div>
        </div>

        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {mockStudents.map((student) => (
            <Card
              key={student.id}
              className="flex flex-col transition-all duration-200 ease-in-out hover:shadow-lg hover:-translate-y-1"
            >
              <CardHeader className="flex flex-row items-center gap-4 space-y-0 pb-4">
                <Avatar className="h-12 w-12 border">
                  <AvatarImage src={student.avatar} alt={student.name} />
                  <AvatarFallback>{student.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
                </Avatar>
                <div className="flex-1">
                  <CardTitle>{student.name}</CardTitle>
                  <CardDescription className="text-xs truncate">{student.email}</CardDescription>
                  <div className="flex items-center gap-3 mt-1 text-xs text-muted-foreground">
                    {student.averageRating && (
                      <span className="inline-flex items-center gap-1">
                        <Star className="h-3 w-3 text-primary/80"/> {student.averageRating.toFixed(1)}
                      </span>
                    )}
                    {student.progress && (
                      <span className="inline-flex items-center gap-1">
                        <TrendingUp className="h-3 w-3 text-primary/80"/> {student.progress}%
                      </span>
                    )}
                  </div>
                </div>
              </CardHeader>
              <CardContent className="flex-1 pt-0">
                <div className="space-y-2 mb-4">
                  <h4 className="text-sm font-medium inline-flex items-center gap-1.5">
                    <BookOpen className="h-4 w-4 text-primary/80"/> Subjects
                  </h4>
                  <div className="flex flex-wrap gap-1">
                    {student.subjects.map((subject) => (
                      <Badge key={subject} variant="secondary">{subject}</Badge>
                    ))}
                  </div>
                </div>
                {student.lastSession && (
                  <p className="text-xs text-muted-foreground">
                    Last Session: {format(student.lastSession, 'PP')}
                  </p>
                )}
              </CardContent>
              <CardFooter className="gap-2 pt-4">
                <Button size="sm" className="w-full" onClick={() => handleScheduleClick(student)}>
                  <CalendarPlus className="mr-2 h-4 w-4" /> Schedule
                </Button>
              </CardFooter>
            </Card>
          ))}
        </div>

        <ScheduleSessionModal
          isOpen={isScheduleModalOpen}
          onClose={() => setIsScheduleModalOpen(false)}
          role="TUTOR"
          availableUsers={selectedStudent ? [selectedStudent] : []}
        />
      </div>
    </DashboardWrapper>
  );
} 